@use "./scss/" as *;

@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700;900&display=swap");

* {
    padding: 0;
    margin: 0;
    //kich thuoc box giu nguyen khi co them padding
    box-sizing: border-box;
    -webkit-tap-highlight-color: transparent;
}

html {
    font-size: 1vw;
    color: pink;

    @include tablet {
        font-size: 2vw;
    }

    @include mobile {
        font-size: 3vw;
    }
}

body {
    font-family: $font-family;
    font-weight: 400;
    line-height: 1.5;
    width: 100vw;
    background-color: $body-bg;
    color: $txt-color;

    @include mobile{
        margin-bottom: 3rem;
    }

}

a{
    text-decoration: none;
    color: unset;
}

@media(hover: hover){
    a:hover{
        color: $main-color;
    }
}

button, input{
    outline: 0;
}

ul {
    list-style-type: none;
}

img{
    max-width: 100%;
}

iframe {
    border: 0;
}

.container {
    max-width: 1660px;
    margin: auto;
}

.mb-3{
    margin-bottom: 3rem;
}

.mb-2{
    margin-bottom: 2rem;
}

.section {
    padding: 0 2rem;
}

.section__header {
    @include flex(center, space-between);
}
